//  
.bullet_list{
  li{
    padding-left: 10px;
    position: relative;
    line-height: 2rem;
    & + li{
      margin-top: 10px;
    }
    &::after{
      @include before_after;
      width: 3px;
      height: 3px;
      border-radius: 1.5px;
      background-color: var(--cff);
      position: absolute;
      top: 10px;
      margin-top: -2px;
      left: 0;
    }
  }
}

