.align {
  display: flex;
  align-items: center; // (default)
  justify-content: space-between; // (default)
  width: 100%;
  // align       
  &.column {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: space-between;
  }
  //    :: start / center / end
  &.center {
    justify-content: center;
  }
  &.start {
    justify-content: flex-start;
  }
  &.end {
    justify-content: flex-end;
  }

  //    :: bottom / top
  &.bottom {
    align-items: flex-end;
  }
  &.top {
    align-items: flex-start;
  }
  //  
  .item {
    text-align: center;
    align-items: center;
    display: inline-flex;
    //     20px (gap )
    & + .item {
      margin-left: 10px;
    }
  }
  // align    (    )
  .lft {
    display: flex;
    align-items: center;
  }
  .rgt {
    display: flex;
    align-items: center;
  }
}