//  font-weight  
.bold {
  &800 {
    font-weight: 800;
  }
  &700 {
    font-weight: 700;
  }
  &600 {
    font-weight: 600;
  }
  &500 {
    font-weight: 500;
  }
  &400 {
    font-weight: 400;
  }
}

.nowrap {
  white-space: nowrap;
}
