//  margin, padding spacing    .
//spacing 2px  .
//spacing 0px ~ 40px  .

@for $value from 0 through 100 {
  // 2px  
  // $value: $i * 2;
  .m {
    &l_#{$value} {
      margin-left: #{$value}px;
    }
    &r_#{$value} {
      margin-right: #{$value}px;
    }
    &t_#{$value} {
      margin-top: #{$value}px;
    }
    &b_#{$value} {
      margin-bottom: #{$value}px;
    }
    &tb_#{$value} {
      margin-top: #{$value}px;
      margin-bottom: #{$value}px;
    }
    &lr_#{$value} {
      margin-left: #{$value}px;
      margin-right: #{$value}px;
    }
    // margin   
    &g_#{$value} {
      margin: #{$value}px;
    }
  }
  .p {
    &l_#{$value} {
      padding-left: #{$value}px;
    }
    &r_#{$value} {
      padding-right: #{$value}px;
    }
    &t_#{$value} {
      padding-top: #{$value}px;
    }
    &b_#{$value} {
      padding-bottom: #{$value}px;
    }
    &tb_#{$value} {
      padding-top: #{$value}px;
      padding-bottom: #{$value}px;
    }
    &lr_#{$value} {
      padding-left: #{$value}px;
      padding-right: #{$value}px;
    }
    // padding   
    &d_#{$value} {
      padding: #{$value}px;
    }
  }

  //
  .gap {
    &_#{$value} {
      gap: #{$value}px;
    }
  }
}
