//APP   scss 

.inner{
  padding: 20px;
}
hr{
  width: 100%;
  height: 5px;
  margin: 0;
  border: 0;
  background-color: var(--bgf5);
}
.order{
  &_visual{
    position: relative;
    //swiper    
    background: #f3f4f5;
    text-align: center;
    > button{
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  .red_text{
    color: var(--red-text) !important;
  }
}